.gradeContent {
  width: 500px;
  height: 460px;
  background: url("../../../../assets/images/interact/vote-bg.png") no-repeat;
  background-size: 100% 100%;
  padding: 20px 0 20px 20px;
}
.gradeContent .grade-head h2 {
  font-size: 18px;
  font-weight: bold;
}
.gradeContent .grade-head p {
  font-weight: bold;
}
.gradeContent .grade-main {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding-top: 40px;
}
.gradeContent .grade-main div {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 10px 40px 10px 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fdf0cc;
  font-size: 45px;
  font-weight: 700;
  color: #f7b500;
  cursor: pointer;
}
.gradeContent .grade-main div.active_grade {
  background: #f7b500;
  color: white;
}
.gradeContent .grade-main div:nth-child(4n) {
  margin-right: 0;
}
.gradeContent .grade-main img {
  cursor: pointer;
  width: 100%;
  height: 100%;
}
.gradeWrap .ant-modal-content {
  background: none;
}
